import { RouterModule, Routes } from '@angular/router';
import { DownloadAppsPage } from './download-apps.page';
import { RouteDataType } from '@core/services/route.service';
import { NgModule } from '@angular/core';
import { AppsPageGuard } from '@core/guards/apps-page.guard';

const routes: Routes = [
	{
		path: '',
		component: DownloadAppsPage,
		data: {
			[RouteDataType.IS_HEADER_VISIBLE]: false,
			[RouteDataType.IS_MOBILE_HEADER_VISIBLE]: false,
			[RouteDataType.IS_FOOTER_VISIBLE]: false,
			[RouteDataType.IS_BOTTOM_NAVBAR_VISIBLE]: false,
		},
		canActivate: [AppsPageGuard],
	},
];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule],
})
export class DownloadAppsPageRoutingModule {}
