import { isPlatformBrowser } from '@angular/common';
import { Inject, Injectable, PLATFORM_ID } from '@angular/core';
import { ActivatedRouteSnapshot, Router, RouterStateSnapshot, UrlTree } from '@angular/router';
import { APP_ROUTE } from '@core/constants';
import { Observable, map, tap } from 'rxjs';
import { AuthService } from '../data-access/services/auth.service';
import { RouteService } from '@core/services/route.service';
import { MeetingScheduleFacade } from '@feature/meeting-schedule/meeting-schedule.facade';
import { DevicePlateform } from '@core/enums/device-plateform.enum';
import { DevicePlateformService } from '@core/services/device-plateform.service';

@Injectable({
	providedIn: 'root',
})
export class AppsPageGuard {
	constructor(
		@Inject(PLATFORM_ID) private readonly platformId: any,
		private readonly plateformService: DevicePlateformService
	) {}

	private openUrl(url: string): void {
		window.location.href = url;
	}

	canActivate(): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
		if (isPlatformBrowser(this.platformId)) {
			const PLATFORM = this.plateformService.getPlatform();

			switch (PLATFORM) {
				case DevicePlateform.ANDROID:
					this.openUrl(APP_ROUTE.external.googlePlayStoreApp);
					return false;
				case DevicePlateform.IOS:
					this.openUrl(APP_ROUTE.external.appStoreApp);
					return false;
				case DevicePlateform.HUAWEI:
					this.openUrl(APP_ROUTE.external.huaweiStoreApp);
					return false;
				default:
					return true;
			}
		}

		return false;
	}
}
